body {
  background-color: #d1ecec;
}

ul {
  justify-content: center;
  list-style: none;
  display: flex;
}

p {
  margin: 0;
  padding: 0;
}

img {
  width: 300px;
}

.js-characters-list {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  row-gap: 10px;
  display: flex;
}

.js-character-item {
  flex-direction: column;
  align-items: center;
  gap: 5px;
  display: flex;
}

.js-character-name {
  font-size: 20px;
}

.js-character-status {
  font-size: 16px;
}

.js-load-more {
  width: 300px;
  color: #fff;
  background-color: #156b6b;
  margin: auto;
  padding: 6px;
  font-size: 20px;
  display: block;
}

